<template>
  <v-container class="m-auto container-style spacing-playground">
    <div class="mt-5" v-if="currentFile">
      <v-progress-linear v-model="progress" color="light-blue" height="25" reactive>
        <strong>{{ progress }} %</strong>
      </v-progress-linear>
    </div>

    <v-row no-gutters justify="center" align="center" class="mt-2">
      <v-col cols="8">
        <v-file-input show-size label="File input" @change="selectFile"></v-file-input>
      </v-col>
      <v-col cols="2" class="pl-2">
        <v-btn color="indigo" dark @click="upload">
          Upload
          <v-icon right dark>mdi-cloud-upload</v-icon>
        </v-btn>
      </v-col>
    </v-row>

    <v-alert v-if="message" class="ml-15 mr-15" color="blue-grey" border="top" elevation="6" type="info">
      Download the file here!
    </v-alert>

    <v-row v-if="isSuccess" no-gutters justify="center" align="center">
      <v-btn x-large color="success" class="ma-2" :href="path" download>
        Download
        <v-icon right dark>mdi-cloud-download</v-icon>
      </v-btn>
    </v-row>

  </v-container>
</template>

<script>
import UploadService from "../services/UploadFileService";

export default {
  name: "upload-files",
  data() {
    return {
      currentFile: undefined,
      progress: 0,
      message: "",
      isSuccess: false,
      path: ""
    };
  },
  methods: {
    selectFile(file) {
      this.progress = 0;
      this.currentFile = file;
    },
    upload() {
      if (!this.currentFile) {
        this.message = "Please select a file!";
        return;
      }

      this.message = "";

      UploadService.upload(this.currentFile, (event) => {
        this.progress = Math.round((50 * event.loaded) / event.total);
      })
          .then(res => {
            this.isSuccess = res.data.is_success;
            this.path = res.data.path;
            this.progress = 100;
            if (this.isSuccess) {
              this.message = "Download the result here!";
            }
          })
          .catch(() => {
            this.progress = 0;
            this.message = "Could not upload the file!";
            this.currentFile = undefined;
          });
    },
  }
};
</script>

<style scoped>
.container-style {
  height: 300px;
  border: 2px solid black;
  border-radius: 10px;
}
</style>